import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Card, Row, Table, Col, message } from 'antd';
import { createCandidateMassSaveCandidate } from 'services/studentCandidate';
import FormImportDataStudent from './FormImportDataStudent';

const round = (_value, _exp) => {
  if (typeof _exp === 'undefined' || +_exp === 0) return Math.round(_value);

  let value = +_value;
  const exp = +_exp;

  if (Number.isNaN(value) || !(typeof exp === 'number' && exp % 1 === 0)) return NaN;

  // Shift
  value = value.toString().split('e');
  // value = Math.round(+(value[0] + 'e' + (value[1] ? (+value[1] + exp) : exp)));
  value = Math.round(+`${value[0]}e${value[1] ? +value[1] + exp : exp}`);

  // Shift back
  value = value.toString().split('e');
  // return +(value[0] + 'e' + (value[1] ? (+value[1] - exp) : -exp));
  return +`${value[0]}e${value[1] ? +value[1] - exp : -exp}`;
};

const columns = [
  {
    title: 'SBD',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'id',
    key: 'id',
    render: (text, record) => `${record.firstname} ${record.lastname}`,
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Nơi sinh',
    dataIndex: 'pobId',
    key: 'pobId',
  },
  {
    title: 'Giới tính',
    dataIndex: 'genderText',
    key: 'genderText',
  },
  {
    title: 'HK',
    dataIndex: 'permanentAddress',
    key: 'permanentAddress',
  },
  {
    title: 'Mã trường',
    dataIndex: 'schoolId',
    key: 'schoolId',
  },
  {
    title: 'KV',
    dataIndex: 'areaId',
    key: 'areaId',
  },
  {
    title: 'Năm TN',
    dataIndex: 'graduatedYear',
    key: 'graduatedYear',
  },
  {
    title: 'Hệ ĐT',
    dataIndex: 'trainTypeId',
    key: 'trainTypeId',
  },
  {
    title: 'Mã ngành',
    dataIndex: 'enrollStudyFieldId',
    key: 'enrollStudyFieldId',
  },
  {
    title: 'Khối',
    dataIndex: 'testGroup',
    key: 'testGroup',
  },
  {
    title: 'Môn 1',
    dataIndex: 'gradeM1',
    key: 'gradeM1',
  },
  {
    title: 'Môn 2',
    dataIndex: 'gradeM2',
    key: 'gradeM2',
  },
  {
    title: 'Môn 3',
    dataIndex: 'gradeM3',
    key: 'gradeM3',
  },
  {
    title: 'ĐTC',
    dataIndex: 'totalGrade',
    key: 'totalGrade',
    render: (text, record) => round(record.gradeM1 + record.gradeM2 + record.gradeM3, 1),
  },
  {
    title: 'Phương án TS',
    dataIndex: 'esId',
    key: 'esId',
  },
];

class ImportDataStudentPage extends Component {
  state = { lstDataStudent: [] };
  uploadData = lstDataStudent => {
    this.setState({ lstDataStudent });
  };

  cancelUpload = () => {
    this.setState({
      lstDataStudent: [],
    });
  };

  saveDataUpload = async () => {
    const { lstDataStudent } = this.state;
    await createCandidateMassSaveCandidate({ candidates: lstDataStudent });
    message.success('Lưu dữ liệu thành công');
    this.setState({
      lstDataStudent: [],
    });
  };

  render() {
    const { lstDataStudent } = this.state;
    return (
      <Card title="Nhập khẩu dữ liệu">
        <Row>
          <Col span={24}>
            <FormImportDataStudent uploadData={this.uploadData} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: true }}
              rowKey="code"
              bordered
              dataSource={lstDataStudent}
              columns={columns}
            />
          </Col>
          {lstDataStudent.length > 0 && (
            <Col offset={8} span={12}>
              <BtnSave onClick={this.saveDataUpload} />
              <BtnCancel onClick={this.cancelUpload} />
            </Col>
          )}
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ImportDataStudentPage />
  </Page>
);



