import React, { Component } from 'react';
import { Form, Button, Upload, Icon, Row, Col } from 'antd';
import { BtnSave } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { requiredRule } from 'utils/validateRules';
import { uploadFileCreateCandidate } from 'services/studentCandidate';

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 4 },
  colon: false,
};

@Form.create({ name: 'formImportDataStudent' })
class FormImportDataStudent extends Component {
  state = {};

  normFile = e => {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList.slice(-1);
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        console.log('error: ', err);
        return;
      }
      this.submituploadFileCreateCandidate(values);
    });
  };

  submituploadFileCreateCandidate = async values => {
    const res = await uploadFileCreateCandidate({
      file: values.file[0].originFileObj,
      trainTypeId: values.trainTypeId,
    });
    this.props.uploadData(res.data.savedCandidates);
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Row>
        <Col span={24}>
          <Form onSubmit={this.handleSubmit} {...formItemLayout}>
            <Form.Item label="Hệ" hasFeedback>
              {getFieldDecorator('trainTypeId', {
                rules: [requiredRule],
              })(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
            <Form.Item label="File dữ liệu">
              {getFieldDecorator('file', {
                rules: [requiredRule],
                valuePropName: 'fileList',
                getValueFromEvent: this.normFile,
              })(
                <Upload accept=".xlsx" beforeUpload={() => false} listType="text">
                  <Button>
                    <Icon type="upload" /> Chọn file
                  </Button>
                </Upload>
              )}
            </Form.Item>

            <Form.Item label=" ">
              <BtnSave htmlType="submit" title="Xem dữ liệu" />
            </Form.Item>
          </Form>
        </Col>
      </Row>
    );
  }
}

export default FormImportDataStudent;



